@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2:wght@600&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
*{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  font-family: 'Baloo Thambi 2', cursive;
}


.App-header {
  background-color: black;
  min-height: 100vh;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin); */
  color: white;
}
.heading
{
    /* color: black; */
    display: block;
    margin: auto;
    font-size: 1.5rem;
    padding: 1rem;
    font-family: Impact, sans-serif;
}
.input_values
{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
}
.input_values input
{
   background-color: black;
   color: white;
   border: 1.75px solid white;
   /* margin: 1rem; */
   margin-top: 1rem;
   margin-bottom: 1rem;
   width: 75%;
   height: 4vh;
}
.input_values input::-webkit-input-placeholder  {
   padding: 0.5rem;
   /* padding-top: 1rem;
   padding-bottom: 1rem; */
    color: rgba(255, 255, 255, 0.664);
  }
.input_values input:-ms-input-placeholder  {
   padding: 0.5rem;
   /* padding-top: 1rem;
   padding-bottom: 1rem; */
    color: rgba(255, 255, 255, 0.664);
  }
.input_values input::placeholder  {
   padding: 0.5rem;
   /* padding-top: 1rem;
   padding-bottom: 1rem; */
    color: rgba(255, 255, 255, 0.664);
  }
  #contact
  {
      background-color: #2a85cd;
      color: white;
      border-radius: 5px;
      width: 30%;
      font-size: 1rem;
      margin: 1rem;
      padding: 0.5rem;
  }
  #contact:hover
  {
      cursor: pointer;
  }
  #content
  {
      display: flex;
      flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
    padding: 1.5rem;
  }
  #content div{
      margin: 1rem;
      font-size: 1.5rem;
  }
  .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	/* background-color: rgba(255,255,255,0.2); */
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
 .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}
#last
{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content:  space-between;
    border-top: 3px solid white;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-top: 1.25rem;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}
#last div
{
    font-size: large;
}
#last div:hover
{
    cursor: pointer;
}
.f1
{
    margin: 2rem;
}
.f2
{
    margin: 1rem;
}
#font
{
    color: white;
    font-weight: bold;
    font-size: x-large;
}
#content1 div
{
color: rgba(255, 255, 255, 0.753);
}
#fil
{
    color: white;
}
#head
{
    background-color: white;
    color: black;
    min-height: 100vh;
}
.top
{
    border-bottom: 2px solid rgba(0, 0, 0, 0.26);
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin: 0.5rem;
}
.box1
{
    display: flex;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;
    text-align: left;
    font-size: xx-large;
}
.box1 img
{
    padding-right: 0.5rem;
}
.cross
{
    color: blue;
}
.box2
{
    display: flex;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;
    text-align: right;
    width: 50%;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1.2rem;
}
.box2 button
{
    border: 2px solid blue;
}
.box2 button:hover
{
    cursor: pointer;
}
.cross:hover
{
    cursor: pointer;
}
.body
{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
}
.body div{
    margin: 2rem;
    font-size: xx-large;
    border-bottom: 2px solid rgba(0, 0, 0, 0.26);
    width: 80%;
    padding-left: 1rem;
    padding-bottom: 1rem;
    text-align: left;
}
#end
{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-evenly;
    height: 20vh;
}
#end button{
    width: 90%;
    height: 5vh;
    font-size: large;
    border: 4px solid blue;
    border-radius: 0.5rem;
    font-family: 'Baloo Thambi 2', cursive;
}
#end button:hover
{
    cursor: pointer;
}
.btn2
{
    background-color: blue;
    color: white;
}
.btn1
{
    background-color: white;
    color: blue;
    border-color: blue;
}
@media screen and (min-width: 1000px) {
    #end button{
      height: 7vh;
    }
  }
